@use "../variables" as *;

$input-bg: #fff;

.subscribe {
  padding: 100px 20px;
  background: linear-gradient(90deg, #dd7833, #c9332f);
  color: $color-white;
  margin-bottom: 0 !important;
  &_container {
    max-width: 550px;
    margin: 0 auto;
    text-align: center;
  }
  &_title {
    font-size: 2.125rem;
    margin: 27px 0 25px;
  }

  &_input-box {
    display: flex;
    &_field {
      flex: 1;
      border: none;
      background-color: $input-bg;
      border-radius: 5em 0 0 5em;
      padding: 11px 30px;
      width: 100%;
      &:focus {
        outline: none;
      }
    }
    &_btn {
      background-color: $input-bg;
      border: none;
      border-radius: 0 5em 5em 0;
      height: 56px;
      padding: 0 30px 0 20px;
      position: relative;
      color: $font-color-main;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 0.875rem;
      transition: color 0.3s ease;

      &:hover {
        color: $color-red-hover;
      }

      &::before {
        content: "";
        width: 1px;
        height: 60%;
        background-color: #dcdcdc;
        left: 0;
        top: 50%;
        position: absolute;
        transform: translateY(-50%);
      }
    }
  }
  &_agreement {
    margin-top: 2rem;
    display: inline-block;
    input {
      display: inline-block;
      margin-right: 0.5rem;
      width: 1.15em;
      height: 1.15em;
      border: 0.15em solid $font-color-main;
      border-radius: 0.15em;
      vertical-align: bottom;
    }

    a {
      text-decoration: underline;
      transition: opacity 0.3s ease;
      &:hover {
        opacity: 0.75;
      }
    }
  }
}
